/* Standard List Styles */
.list-panel {
  display: flex;
  flex-direction: column;
  width: 300px;
  min-width: 300px;
  margin: 5px;
  box-sizing: border-box;
}

.list-item {
  height: 50px;
  margin: 1px 0;
  padding: 4px;
  box-sizing: border-box;
  cursor: pointer;
}

.list-item:hover {
  background-color: #e2e2e2;
  border-radius: 2px;
}

.selected-item {
  background-color: #adadadf2;
}

/* Badge List Styles */
.badge-list {
  display: flex;
  flex-wrap: wrap;
  padding: 10px 0 0;
}

.badge {
  display: flex;
  width: 175px;
  height: 50px;
  margin: 5px;
  border-radius: 2px;
  background-color: #d3d3d3;
  box-shadow: 0px 0px 2px 1px rgba(0,0,0,0.34);
}

.badge-image {
  height: 44px;
  width: 44px;
  padding: 3px;
  border-radius: 25px;
}

.badge-text {
  width: 125px;
  display: flex;
  align-items: center;
}
.app {
  display: flex;
  flex-direction: column;
  min-height: 0;
  min-width: 1000px;
}

.app-header {
  padding: 5px;
  box-sizing: border-box;
  background-color: #f3f3f3;
  text-align: center;
  color: Black;
  box-shadow: 0px 0px 5px 1px rgba(0,0,0,0.34);
}

.app-title {
  font-size: 1.5em;
}

.sub-title {
  text-align: center;
  font-size: 1.3em;
  font-weight: bold;
}

.sub-heading {
  text-align: center;
  font-size: 1.2em;
  font-weight: bold;
}

.standard-margins {
  margin: 6px;
}

/* Layout Styles */
.flex {
  display: flex;
  min-height: 0;
}

.flex-column {
  display: flex;
  flex-direction: column;
  min-height: 0;
}
body {
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  font-family: "Trebuchet MS", "Lucida Grande", "Lucida Sans Unicode", "Lucida Sans", Tahoma, sans-serif;
}
